            /* new stuff from not 5 years ago */

            .feed-wrapper {
              position: relative;
            }

            @media (min-width: 1100px) {
              .feed-controls { width: 300px; float: right; }
              .feed-controls.fixed { position: fixed; top: 0; right: calc((100% - 1100px)/2); }
              .feed-main { width: 700px; float: left; }
            }

            @media (max-width: 1099px) {
              .feed-controls { display: none; }
              .feed-main { width: auto; float: none; }
            }

            .ss-container {
              width: 1100px;
              margin: 0 auto;
              position: relative;
            }
            @media (max-width: 1099px) {
              .ss-container {
                width: auto;
              }
            }

    /* -- */

